import React, {useState} from 'react'
import {useSelector} from 'react-redux'
import {Link } from 'react-router-dom'
import Logo from '../../images/logo/icon/logowiithname.png'


const Footer = () => {

   const user = useSelector((state)=> state.user.user)

  return (
    <footer style={{marginTop: '50px'}} className="footer-main">
    <div className="footer-top">
       <div className="container">
          <div className="row">
             <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <Link to="/"><img src={Logo} alt=""/></Link>
                   <p className="theme-description">
                   We are one of the World First NFT marketplace on Binance Smart .With Cheaper transactions charges and commission from other marketplaces on Binance SmartChain
                       </p>
                   <div className="footer-links clearfix">
                      <div className="footer-icon-box">
                         <a href=" https://www.facebook.com/digiplazacrypto" title="Facebook" target="_blank" style={{color: '#3b5998'}}>
                         <i className="fab fa-facebook-f"></i>
                         </a>
                      </div>
                      <div className="footer-icon-box">
                         <a href="https://twitter.com/digiplazacrypto"  target="_blank" title="Twitter" style={{color: '#00acee'}}>
                         <i className="fab fa-twitter"></i>
                         </a> 
                      </div>
                      <div className="footer-icon-box">
                         <a href="https://discord.gg/AFqe6k27" title="discord"  target="_blank" style={{color: '#0e76a8'}}>
                         <i className="fab fa-discord"></i>
                         </a>
                      </div>
                      <div className="footer-icon-box">
                         <a href="https://t.me/DigiSmartChain" title="telegram"  target="_blank" style={{color: '#0e76a8'}}>
                         <i className="fab fa-telegram"></i>
                         </a>
                      </div>
                      <div className="footer-icon-box">
                         <a href="https://www.youtube.com/channel/UCzMT0JYMsyjMRm4EmsOb_1g" title="youtube"  target="_blank" style={{color: '#0e76a8'}}>
                         <i className="fab fa-youtube"></i>
                         </a>
                      </div>
                      <div className="footer-icon-box">
                         <a href="https://www.reddit.com/r/Digiplaza/" title="reddit"  target="_blank" style={{color: '#0e76a8', marginTop: '5px'}}>
                         <i className="fab fa-reddit"></i>
                         </a>
                      </div>

                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">My account</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         {user? (<>
                           <li><Link to={'/creator/create'}>Mint your Artwork</Link></li>
                           <li><a href={`/${ user.publicAddress}`}>My Profile</a></li>
                         </>):(<>
                           <li><a href={'/'}>Connect to metamask</a></li>
                         </>)}
                        
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Need Help?</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><a target="_blank" href="/contactus">Help and support</a></li>
                         <li><a target="_blank" href="/faq">FAQ</a></li>
                         <li><a href="/contactus">Contact us</a></li>
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Buy an Item</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><a href="/browse?category=collectibles">Browse Collectibles</a></li>
                         <li><a href="/creators">Browse Creators</a></li>
                         <li><a href="/browse">Where to buy NFTs</a></li>
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Nft MarketPlace</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><a target="_blank" href="/privacy-policy">Privacy Policy</a></li>
                         <li><a target="_blank" href="/terms-of-use">Terms of use</a></li>
                      </ul>
                   </div>
                </div>
             </div>
          </div>
       </div>
    </div>
    <div className="footer-copyright">
       <div className="container">
          <div className="row">
             <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                <div className="copyright-text text-center">
                   <p>Copyright 2021 <a href="/">Digiplaza</a> All Rights Reserved.</p>
                </div>
             </div>
          </div>
       </div>
    </div>
 </footer>
  )
}
export default Footer
